import React from 'react'
import axios from 'axios'
import {withRouter} from 'react-router-dom'
import {
    FacebookIcon,TwitterIcon,LinkedinIcon,  EmailIcon,PinterestIcon, InstapaperIcon

} from 'react-share';
import Instagram from 'react-share-icons/lib/Instagram';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
class Contact extends React.Component{
    constructor(a)
    {
        super(a)
        this.state={
            name:'',
            email:'',
            subject: '',
            body: '',
            modal:false
        }
        this.handleName=this.handleName.bind(this);
        this.handleEmail=this.handleEmail.bind(this);
        this.handleSubject=this.handleSubject.bind(this);
        this.handleBody=this.handleBody.bind(this);
        this.submitEmail=this.submitEmail.bind(this)
    
        this.toggle = this.toggle.bind(this);
    
    }
    toggle() {
        this.setState(prevState => ({
          modal: !prevState.modal,
          name: "",
          email: '',
          body : '',
          subject: ''
        }));
      }
    componentDidMount()
    {
        this.props.history.push('/contact');
        console.log("pushed ");
    }
    handleName(value)
    {
        console.log(value);
        this.setState({name: value.target.value});
    }
    handleEmail(value)
    {
        console.log(value);
        this.setState({email: value.target.value});
    }
    handleSubject(value)
    {
        console.log(value);
        this.setState({subject: value.target.value});
    }
    handleBody(value)
    {
        console.log(value);
        this.setState({body: value.target.value});
    }
    submitEmail(e)
    {
        e.preventDefault();
        console.log("Your Values are",this.state);
        let data={};
        data.name=this.state.name;
        data.email=this.state.email;
        data.subject=this.state.subject;
        data.body=this.state.body;
        console.log("sending email");
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/contact`,{email:this.state.email,name:this.state.name,contact:this.state.contact,body:this.state.body,subject:this.state.subject}).then(res => {
            console.log("Your Email is sent ",res)
            if(res.status=200)
            {
                this.toggle();
            }
        })
    }
    
    render()
    {
        return (
            <div style={{paddingTop:"5%"}}>
                <div className="container">
                    <div className="row">
                        <div className="col-md-6 ">
                            <h1>Contact Us</h1>
                            <p>We's love to hear from you. Feel free to let us know how you feel about conceptionary. Any ideas who we can improve ?</p>
                            
                            <div className="" style={{border:"1px solid #4a4545",padding:20}}>
                                <form action="" className="form-horizontal">
                                <div class="form-group">
                                    <label for="email">Name :</label>
                                    <input value={this.state.name} style={{border:'1px solid #4a4545'}} type="name" placeholder="Enter your name" onChange={this.handleName} class="form-control" id="email" />
                                </div>
                                
                                <div class="form-group">
                                    <label for="pwd">Subject:</label>
                                    <input  value={this.state.subject} style={{border:'1px solid #4a4545'}} type="text" placeholder="Enter Subject"  onChange={this.handleSubject} class="form-control" id="pwd" /> 
                                </div>
                                <div class="form-group">
                                    <label for="pwd">Email:</label>
                                    <input  value={this.state.email} style={{border:'1px solid #4a4545'}} type="email"  onChange={this.handleEmail} placeholder="Enter your Email" class="form-control" id="pwd" required /> 
                                </div>
                                <div className="form-group">
                                <label htmlFor="body">Body :</label>
                                <textarea required  value={this.state.body} style={{border:'1px solid #4a4545'}}  onChange={this.handleBody} placeholder="Enter your Message" className="form-control" rows="4">
                                    
                                </textarea>
                                </div>
                                <div className="text-right">
                                <button type="submit" class="btn btn-info" onClick={this.submitEmail}>Submit</button>
                                <Modal isOpen={this.state.modal} toggle={this.toggle} className="animated zoomIn" style={{top:'30%'}}>
                                <ModalHeader toggle={this.toggle} style={{padding:'0',paddingRight:'1rem'}}></ModalHeader>
                                <ModalBody className="text-center">
                                    <h3>Thanks for getting in touch</h3>
                                </ModalBody>
                                {/* <ModalFooter>
                                    <Button color="danger" onClick={this.toggle}>Close</Button>
                                </ModalFooter> */}
                                </Modal>
                                </div>
                                </form>
                                
                            </div>
                          <br/><br/>
                        </div>
                        
                    <div className="col-md-4 offset-md-2" >
                        <div>
                            <br/>   
                            <p><b>visit us on social media or contact us</b></p>
                            <p >
                                <a href="https://twitter.com/Conceptionaryio" style={{display:"inline-block",padding:8}}><TwitterIcon size={30} /></a>
                                <a href="https://www.pinterest.com/conceptionary/" style={{display:"inline-block",padding:8}}><PinterestIcon size={30} /></a>
                                <a href="https://www.facebook.com/conceptionary/" style={{display:"inline-block",padding:8}}><FacebookIcon   Icon size={30} /></a>
                                <a href="https://instagram.com/conceptionary/" style={{display:"inline-block",padding:8}}><InstapaperIcon size={30} /></a>
                            
                            </p>
                            <div style={{color:'#32217a'}}>
                                <p>Copyright &copy; 2019 conceptionary.io</p>
                                
                            </div>
                        </div>
                    </div>
                        
                    </div>
                </div>
            
            </div>
        );
    }
}
export default withRouter(Contact);